







export default {
	name: "graceStart",
	props:{
		starNum : {
		  type : Number,
		  default : 5
		},
		value : {
		  type : Number,
		  default : 0
		},
		starImg: {
			type : Array,
			default : function(){
				return [
					"../../static/imgs/star.png",
					"../../static/imgs/star-active.png"
				];
			}
		},
		canTap : {
		  type : Boolean,
		  default : true
		}
	},
	data() {
		return {
			currentNumber : 0
		}
	},
	created : function(){
		this.currentNumber = this.value;
	},
	methods: {
		changNum : function(e){
			if (!this.canTap){return ;}
			var val = Number(e.currentTarget.dataset.val);
			this.currentNumber = val + 1;
			this.$emit("starChange", this.currentNumber);
		}
	}
}
